.Button {
  height: 50px;
  width: 50px;
  margin-top: 25px;
  outline: none;
  border: none;
  font-size: 18px;
  color: rgba(204, 204, 204, 0.7);
  font-weight: 100;
}

.Button:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
  color: rgba(0, 0, 0, 1);
}



